<script>
    import { selectionStore } from "./selectionStore.js";
    const { isSaved } = selectionStore;
    import { appLang } from '../constants';

    export let isRegion = true;
</script>

<footer class="modal-card-foot is-center">
    <div class="buttons">
        <button class="button button-close is-link is-light" on:click={() => selectionStore.empty(isRegion)}>
            {appLang === 'en' ? 'Clear selection' : 'Vider la slection'}
        </button>
        {#if $isSaved}
            <button class="button is-link" on:click={() => null}>
                <span>
                    <i class="fa-solid fa-gear"></i>
                    {appLang === 'en' ? 'Go to treatment' : 'Accder au traitement'}
                </span>
            </button>
        {:else}
            <button class="button is-link" on:click={() => selectionStore.save(isRegion)}>
                <span>
                    <i class="fa-solid fa-floppy-disk"></i>
                    {appLang === 'en' ? 'Save selection' : 'Sauvegarder la slection'}
                </span>
            </button>
        {/if}
    </div>
</footer>
